
.container{
    max-width: 1350px; 
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between; 
    align-items: center;
    padding: 17px 100px;
}
.list{
    display: flex;
    margin: 0 auto; ;
    gap: 45px;  
    list-style-type: none;
    
}
.item{
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 16px;
    color: #000000;
}
.user{
    display: flex;
    flex-direction: row;
    justify-content: center; 
    align-items: center;
    gap: 25px;
}
.logo{
width: 82px;
height: 56px;

}
.logo img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.wrapper{
    display: flex;
    flex-direction: row;
    justify-content: center; 
    align-items: center;
    gap: 111px;
   
}