.container{
    background-image: url(../images/hero-background.png) , url(../images/hero-overlay.png);
    background-size: cover;
    background-position: center;
    width: 100%;
    /* max-width: 1200px; */
    /* margin: 0 auto; */
    /* padding: 0 20px; */
    padding: 100px 0 100px 0;
    margin: 0;
}
.title{
    font-family: var(--font-family);
font-weight: 600;
font-size: 40px;
color: #fff;
}
.text, .date{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 24px;
    /* text-align: right; */
    color: #fff;
    margin: 0px;
}
.line{
    border: 3px solid #fff;
    width: 144px;
    height: 0px;
    transform: rotate(-90deg);
    margin: 80px 0px;
}
.cover{
    display: flex;
    align-items: center;
    justify-content: center;

}
.input{
border-radius: 10px 0 0 10px;
width: 625px;
height: 42px;
background: #d9d9d9;
padding-left: 29px;
font-family: var(--font-family);
font-weight: 500;
font-size: 14px;
color: #878787;
}
.input_btn{
border-left: 2px solid #000;
border-radius: 0 10px 10px 0;
width: 45px;
background: #ffb36c;
padding: 9px 10px;
}
.input_wrapper{
display: flex;
align-items: center;
justify-content: center;
margin-top: 60px;

}